.hotels{
    padding-top: 86px;
    padding-bottom: 80px;
    background: url('/img/hotelsbg.png') center;
    position: relative;
    .slide-count-wrap{
        text-align: center;
        .current{
                font-size: 36px;
                font-family: "Open Sans";
                color: rgb(64, 64, 64);
                text-transform: uppercase;
        }
        .total{
                font-size: 15px;
                font-family: "Open Sans";
                color: rgb(241, 160, 20);
                text-transform: uppercase;
        }
    }
    .slider{
        .card{
            margin: 15px;
            border-radius: 0;  
            .card-img-top{
                border-radius: 0;  
            }
            .card-body{
                h5 {
                    font-size: 16px;
                    font-family: "Open Sans";
                    color: rgb(64, 64, 64);
                    font-weight: bold;
                    text-transform: uppercase;
                  }
                  
                 p {
                    font-size: 14px;
                    font-family: "Open Sans";
                    color: rgb(241, 160, 20);
                  }
                  
               
            }
        }
        .slick-dots{
            right: 15px;
            top: -10%;
            text-align: end;
            li{
                width: 12px;
                button{
                    &::before{
                        font-size: 50px;
                    }
                }
            }
        }
    }
}