footer {
  background: url('../img/bgfooter.png') center;
  background-size: cover;
  padding-top: 80px;
  padding-bottom: 65px;
  h1 {
    font-size: 36px;
    font-family: "Open Sans";
    color: rgb(255, 255, 255);
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 40px;
  }
  h3 {
    font-size: 18px;
    font-family: "Roboto";
    color: rgba(255, 255, 255, 0.702);
    margin-bottom: 40px;
    text-transform: capitalize;
  }
  
  ul {
      padding: 0;
    li {
        p{
                font-size: 14px;
                font-family: "Roboto";
                color: rgba(255, 255, 255, 0.502);
                line-height: 2.143;
        }
        a {
            text-decoration: none !important;
                font-size: 14px;
                font-family: "Roboto";
                color: rgba(255, 255, 255, 0.502);
                line-height: 2.143;
                text-transform: capitalize;
              
          }
      &.adress {
        font-size: 12px;
        font-family: 'Myriad Pro';
        color: rgb(171, 171, 171);
        text-transform: uppercase;
        margin-bottom: 30px;
      }
      &.news {
        font-size: 12px;
        font-family: "Myriad Pro";
        color: rgb(108, 108, 108);
        line-height: 2.5;
        margin-bottom: 20px;
      }
      &.search {
        position: relative;
        margin-bottom: 30px;
        input {
          width: 100%;
          background-color: rgb(255, 255, 255);
          height: 40px;
          border-top-left-radius: 20px;
          border-bottom-left-radius: 20px;
          border-top-right-radius: 20px;
          border-bottom-right-radius: 20px;
          border: none;
          padding-left: 25px;
          &::placeholder {
            font-size: 10px;
            font-family: "Open Sans";
            color: rgb(150, 150, 150);
            text-transform: uppercase;
          }
        }
        button {
          position: absolute;
          right: 0;
          height: 40px;
          border-top-right-radius: 20px;
          border-bottom-right-radius: 20px;
          z-index: 5;
          border: none;
          background: #f1a014;
          font-size: 10px;
          font-family: "Open Sans";
          color: rgb(255, 255, 255);
          font-weight: bold;
          text-transform: uppercase;
          line-height: 1.2;
          width: 94px;
        }
      }
      &.icons {
       .fa {
          border-style: solid;
          border-width: 1px;
          border-color: rgb(193, 193, 193);
          border-radius: 50%;
          background-color: rgb(254, 254, 255);
          font-size: 12px;
          color: rgb(122, 122, 122);
        width: 24px;
        height: 26px;
        text-align: center;
        line-height: 2;
        margin: 0 4px;
        }
      }
      list-style: none;
    }
  }
}