.travel{
    background: url('../img/travel.png') center;
    background-size: cover;
    padding-top: 90px;
    padding-bottom: 200px;
    .title{
       text-align: start;
       max-width: 468px;
       margin-left: auto;
       h3{
           margin-bottom: 30px;
           .sm(font-size,38px);
       }
       h6{
           margin-bottom: 35px;
       }
       p {
           margin-bottom: 35px;
            font-size: 14px;
            font-family: "Open Sans";
            color: rgb(122, 122, 122);
            line-height: 1.286;
           
            a{
                color: #f1a014;
            }
          }
         button{
                font-size: 12px;
                font-family: "Open Sans";
                color: rgb(255, 255, 255);
                font-weight: bold;
                text-transform: uppercase;
                background-color: rgb(241, 160, 20);
                width: 170px;
                height: 45px;
         } 
    }
}