header{
    min-height: 100vh;
    background: url('../img/headerbg.png') center;
    background-size: cover;
    position: relative;
    .sina-nav{
        position: absolute;
        width: 100%;
        .extension-nav{
            background: url('../img/menubg.png') center;
            background-size: cover;
            ul{
                .md(padding,0);
                li{
                    .sm-block({
                       padding: 12% 10px; 
                    });
                   &.phone{
                    span{
                     .sm(display, none);   
                    }   
                   }
                  
                    &.search{
                        a{
                             font-size: 10px;
                            font-family: "Open Sans";
                            color: rgb(253, 254, 255);
                            font-weight: bold;
                            text-transform: uppercase;
                            line-height: 1.2;
                           
                        }
                           
                    }
                    a{
                         font-size: 14px;
                        font-family: "Open Sans";
                        color: rgb(254, 254, 255);
                        font-weight: bold;
                        text-transform: uppercase;
                        line-height: 1.2;
                      
                    }
                       
                }
            }
        }
        .widget-bar{
            background: url('../img/menubg.png') center;
            background-size: cover;
        }
        .sina-brand{
            h1{
                .sm-block({
                   font-size: 30px !important; 
                });
            }
        }
    }
    .headerBody{
        display: flex;
        height: 100vh;
        .headerTitle{
           margin: auto;margin-left: 12%;.lg(margin-left, 10%);.sm(margin-left, 5%);.media-block({
               margin-left:  auto;
           },500px);
            text-transform: capitalize;
            text-align: left;
            h2 {
                font-size: 110px;.md(font-size,80px);.media-block({font-size: 50px;},500px);
                font-family: "Open Sans";
                color: rgb(36, 78, 132);
                line-height: .636;
                font-weight: 700;
                &::after{
                    content: "Welcome";
                    font-size: 12px;
                    color: #404040;
                    font-weight: 700;
                    font-family: 'Open Sans';
                    writing-mode: tb-rl;
                    .media-block({
                        writing-mode: lr;
                        display: flex;
                        line-height: 1.6;
                        margin-left: 2%;
                    },500px);
                }
              }
              h1 {
                font-size: 223px;.md(font-size,150px);.media-block({font-size: 100px;},500px);
                font-family: "Open Sans";
                color: rgb(36, 78, 132);
                line-height: 0.807;
                font-weight: 700;
                &::after{
                    content: "The only one free psd templates of ever";
                    font-size: 12px;
                    color: #404040;
                    font-weight: 700;
                    font-family: 'Open Sans';
                    display: flex;
                    margin-left: 6.6%;
                }
              }
             
         
          
              
        }
    }
    .form{
       padding-bottom: 5%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .md(flex-direction,column);
        .input-container{
            position: relative;
            width: 20%;.xl(width, 18%);.lg(width, 24%);.md(width, 100%);
            align-self: flex-end;
            img{
                position: absolute;
                right: 0;
                top: 10px;
            }
            input{
                border: none;
                border-bottom: 1px solid #f1a014;
                border-radius: 0;
                background: transparent;
               box-shadow: none;
               &::placeholder{
                     font-size: 12px;
                     font-family: "Open Sans";
                     color: rgb(64, 64, 64);
                     font-weight: bold;
                     text-transform: uppercase;
               }
            }
            
        }
      
       .btn {
         background-color: rgb(241, 160, 20);
         width: 170px;
         height: 45px;
         font-size: 12px;
         font-family: "Open Sans";
         color: rgb(255, 255, 255);
         font-weight: bold;
         text-transform: uppercase;
         border-radius: 0%;
         box-shadow: none;
       .lg-block({
         margin: auto;
           margin-top:5%;
       });
       
    }  
}
}



