@import "./params/mix.less";
@import "./params/variables.less";
@import  "./header.less";
@import  "./hotels.less";
@import  "./travel.less";
@import  "./tours.less";
@import  "./people.less";
@import  "./footer.less";


body{
  min-width: 320px;
}
.stars {
  text-decoration: none;
  i {
    color: #f1a014;
    width: 19px;
    height: 19px;
    &:nth-child(5) {
      color: #ddd;
    }
  }
}
.title{
  h3{
      font-size: 50px;
      font-family: "Open Sans";
      color: rgb(122, 122, 122);
      font-weight: bold;
      text-transform: uppercase;
  }
  h6{
      font-size: 12px;
      font-family: "Open Sans";
      color: rgb(64, 64, 64);
      font-weight: bold;
      text-transform: uppercase;
  }
}
img {
  max-width: 100%;
  height: auto;
}
