.tours {
  background: url('../img/toursbg.png') center;
  background-size: cover;
  padding: 90px 0;
  .title {
      text-align: center;
    h3 {
      color: rgb(255, 255, 255);
    }
  }
  .popular {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .card {
      position: relative;
     /*  width: 30%;.lg(width, 40%);.sm(width, 100%); */
     max-width: 370px;
      margin: 15px;
      border-radius: 0;
      .card-img-top {
        border-radius: 0;
      }
      .card-body {
        h5 {
          font-size: 16px;
          font-family: "Open Sans";
          color: rgb(64, 64, 64);
          font-weight: bold;
          text-transform: uppercase;
        }
        p {
          font-size: 14px;
          font-family: "Open Sans";
          color: rgb(241, 160, 20);
        }
      
        .icons {
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          i {
            display: flex;
            p {
              font-size: 10px;
              font-family: "Open Sans";
              color: rgba(64, 64, 64, 0.502);
              font-weight: bold;
              text-transform: uppercase;
              align-self: flex-end;
              margin: 0 8px;
              margin-bottom: 8px;
            }
            img {
              display: block;
              align-self: center;
              margin: 0 8px;
              &:nth-child(1) {
                margin-left: 0;
              }
            }
          }
          a {
            font-size: 26px;
            font-family: "Open Sans";
            color: rgb(241, 160, 20);
            font-weight: bold;
            text-transform: uppercase;
          }
        }
      }
      .rent {
        position: absolute;
        writing-mode: tb-rl;
        font-size: 14px;
        font-family: "Open Sans";
        color: rgb(255, 255, 255);
        font-weight: bold;
        text-transform: uppercase;
        background: url('../img/rental.png') center;
        padding: 10px;
        left: 5%;
      }
    }
  }
}