.people {
  padding-top: 85px;
  padding-bottom: 80px;
  .title {
    text-align: center;
  }
  .slider {
    .item {
      text-align: center;
      margin: 15px;
      img {
        width: 150px;
        margin: auto;
      }
      h3 {
        font-size: 16px;
        font-family: "Open Sans";
        color: rgb(64, 64, 64);
        font-weight: bold;
        text-transform: uppercase;
        text-align: center;
      }
      p {
        font-size: 14px;
        font-family: "Open Sans";
        color: rgb(64, 64, 64);
        line-height: 1.714;
        text-align: center;
        max-width: 363px;
        margin: auto;
      }
    }
    .slick-dots {
      bottom: -50px;
      li {
        width: 12px;
        button {
          &::before {
            font-size: 50px;
          }
        }
      }
    }
  }
}